import * as React from 'react';
import { useQuery } from '@tanstack/react-query';
import { Wrapper, Status } from '@googlemaps/react-wrapper';
import { MarkerClusterer as MarkerClustererClass } from '@googlemaps/markerclusterer';

import Slide from '@mui/material/Slide';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import Button from '@mui/material/Button';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import { TransitionProps } from '@mui/material/transitions';
import ContentCopy from '@mui/icons-material/ContentCopy';

import Loader from '../components/Loader';
import { TruckQuery } from '../api';
import { useLoader, useSnackbar } from '../hooks';
import { Truck, TruckForMap } from '../utils/dataTypes';
import { getType } from '../utils/queryHelpers';
import { stopPropagation } from '../utils/stopPropagation';
import { getMapSettings, center, mapId, zoom } from '../utils/getMapSettings';
import { selectLocation } from '../utils/selectLocation';
import { toStringLocation } from '../utils/toStringLocation';

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>,
) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function MapComponent({
  truckListData,
}: {
  truckListData: TruckForMap[];
}) {
  let mapCenter = center;
  let mapZoom = zoom;
  if (truckListData.length === 1 && truckListData[0].lastLocation) {
    mapCenter = {
      lat: truckListData[0].lastLocation[0],
      lng: truckListData[0].lastLocation[1],
    };
    mapZoom = mapZoom * 2;
  }

  const [geocoder, setGeocoder] = React.useState<google.maps.Geocoder | null>(
    null,
  );
  const [selectedTruckId, setSelectedTruckId] = React.useState<string>('');
  const [selectedTruck, setSelectedTruck] = React.useState<Truck | null>(null);
  const [isTruckLoading, setIsTruckLoading] = React.useState<boolean>(false);

  const {
    // isLoading: isTruckLoading,
    // isFetching: isTruckFetching,
    // data: truck,
    refetch,
  } = useQuery({
    queryKey: ['truckForMap', selectedTruckId],
    queryFn: TruckQuery,
    keepPreviousData: false,
    retry: false,
    enabled: false,
  });

  React.useEffect(() => {
    if (!selectedTruckId) {
      setSelectedTruck(null);
    } else {
      setIsTruckLoading(true);
      const truckItem = truckListData?.find(
        (truck) => truck.id === selectedTruckId,
      );
      const truckLocation = truckItem && truckItem?.lastLocation;

      Promise.all([
        refetch().then((res) => res.data),
        truckLocation &&
          geocoder
            ?.geocode({
              location: { lat: truckLocation[0], lng: truckLocation[1] },
            })
            .then((res) => {
              const location = selectLocation(res.results);
              if (location) {
                return toStringLocation(location.address_components);
              }
              return '';
            }),
      ])
        .then(([truck, location]) => {
          if (truck) {
            setSelectedTruck({ ...truck, lastCity: location || '' });
          } else {
            setSelectedTruck(null);
          }
        })
        .catch(() => setSelectedTruck(null))
        .finally(() => setIsTruckLoading(false));
    }
  }, [selectedTruckId]);

  const handleClickOpen = (truckId: string) => {
    setSelectedTruckId(truckId);
  };

  const handleDialogClose = () => {
    setSelectedTruckId('');
  };

  const handleApiLoad = (status: Status): React.ReactElement => {
    const setSnackbarMessage = useSnackbar();
    const [, setLoaderVisibility] = useLoader();
    React.useLayoutEffect(() => {
      if (status === Status.LOADING) {
        setLoaderVisibility(true);
      }
      if (status === Status.FAILURE) {
        setSnackbarMessage({
          severity: 'error',
          message: 'Error loading maps!',
        });
      }
      if (status === Status.SUCCESS) {
        setGeocoder(new window.google.maps.Geocoder());
      }
      return () => {
        if (status === Status.LOADING) {
          setLoaderVisibility(false);
        }
      };
    }, [status]);
    return null as unknown as React.ReactElement;
  };

  return (
    <>
      <Box
        component="main"
        sx={{ flexGrow: 1, p: 1, height: '100%', width: '100%' }}
      >
        <Wrapper {...getMapSettings()} render={handleApiLoad} />
        {!geocoder ? null : (
          <Map center={mapCenter} zoom={mapZoom} mapId={mapId}>
            <MarkerClusterer>
              {truckListData.map((truck) => {
                return (
                  <Marker
                    key={truck.id}
                    onCLick={() => handleClickOpen(truck.id)}
                    optimized
                    animation={window?.google?.maps?.Animation?.DROP}
                    title={`${truck.truckNumber}\n${truck.status}`}
                    position={{
                      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                      // @ts-ignore
                      lat: truck.lastLocation[0],
                      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                      // @ts-ignore
                      lng: truck.lastLocation[1],
                    }}
                  />
                );
              })}
            </MarkerClusterer>
          </Map>
        )}
      </Box>
      <Dialog
        open={!!selectedTruck}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleDialogClose}
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle>
          <span>
            TRUCK NUMBER: 
            <a href={`./trucks/view/${selectedTruck?.id}`} target="_blank">
              {selectedTruck?.truckNumber}
            </a>
          </span>
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="truck-status">
            {`CURRENT STATUS: ${selectedTruck?.status}`}
          </DialogContentText>
          <DialogContentText id="main-contact">
            <span>
              {'MAIN CONTACT: '}
              <b>{`${
                selectedTruck?.coordinator || selectedTruck?.owner
                  ? // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                    //@ts-ignore
                    getType(selectedTruck?.coordinator || selectedTruck?.owner)
                  : ''
              }`}</b>
              {` ${
                selectedTruck?.coordinator || selectedTruck?.owner
                  ? // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                    //@ts-ignore
                    (selectedTruck?.coordinator || selectedTruck?.owner)
                      .fullName
                  : ''
              }`}
              <IconButton
                size="small"
                onClick={(event) => {
                  stopPropagation(event);
                  navigator.clipboard.writeText(
                    ` ${
                      selectedTruck?.coordinator || selectedTruck?.owner
                        ? // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                          //@ts-ignore
                          (selectedTruck?.coordinator || selectedTruck?.owner)
                            .fullName
                        : ''
                    } ${
                      (selectedTruck?.coordinator || selectedTruck?.owner)
                        ?.phone || ''
                    }`,
                  );
                }}
              >
                <ContentCopy sx={{ height: '16px', width: '16px' }} />
              </IconButton>
            </span>
          </DialogContentText>
          <DialogContentText id="main-contact-number">
            <span>
              {'MAIN CONTACT #: '}
              <a
                href={
                  selectedTruck?.coordinator || selectedTruck?.owner
                    ? `tel:${
                        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                        //@ts-ignore
                        (selectedTruck?.coordinator || selectedTruck?.owner)
                          .phone
                      }`
                    : undefined
                }
                target="_blank"
                onClick={stopPropagation}
              >
                {(selectedTruck?.coordinator || selectedTruck?.owner)?.phone ||
                  ''}
              </a>
            </span>
          </DialogContentText>
          <DialogContentText id="last-location">
            <span>
              {`LAST LOCATION #: ${selectedTruck?.lastCity}`}
              <IconButton
                size="small"
                onClick={(event) => {
                  stopPropagation(event);
                  navigator.clipboard.writeText(`${selectedTruck?.lastCity}`);
                }}
              >
                <ContentCopy sx={{ height: '16px', width: '16px' }} />
              </IconButton>
            </span>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleDialogClose}>Close</Button>
        </DialogActions>
      </Dialog>
      <Loader visible={isTruckLoading} />
    </>
  );
}

const render = (status: Status): React.ReactElement => {
  const setSnackbarMessage = useSnackbar();
  if (status === Status.FAILURE) {
    setSnackbarMessage({
      severity: 'error',
      message: 'Error loading maps!',
    });
  }
  return (
    <>
      <Loader visible={status === Status.LOADING} />
    </>
  );
};

function Map({
  center,
  zoom,
  mapId,
  children,
}: {
  center: google.maps.LatLngLiteral;
  zoom: number;
  mapId: string;
  children?: React.ReactNode;
}) {
  const mapRef = React.useRef(null);
  const [map, setMap] = React.useState<google.maps.Map>();

  React.useEffect(() => {
    if (mapRef.current && !map) {
      setMap(
        new window.google.maps.Map(mapRef.current, {
          center,
          zoom,
          mapId,
        }),
      );
    }
  }, [mapRef, map]);

  const child = React.Children.only(children);

  return (
    <>
      <div style={{ height: '100%', width: '100%' }} ref={mapRef} id="map" />
      {React.isValidElement(child) &&
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        React.cloneElement(child, { map })}
    </>
  );
}

function MarkerClusterer({
  map,
  children,
}: {
  map?: google.maps.Map;
  children?: React.ReactNode;
}) {
  const [clusterer, setClasterer] = React.useState<MarkerClustererClass>();

  React.useEffect(() => {
    if (!clusterer) {
      setClasterer(new MarkerClustererClass({}));
    }

    return () => {
      if (clusterer) {
        clusterer.setMap(null);
      }
    };
  }, [clusterer]);

  React.useEffect(() => {
    if (clusterer && map) {
      clusterer.setMap(map);
    }
  }, [map]);

  React.useEffect(() => {
    if (clusterer) {
      clusterer.render();
    }
  }, [map, children]);
  clusterer?.render();

  return (
    <>
      {React.Children.map(children, (child) => {
        if (React.isValidElement(child)) {
          // set the map prop on the child component
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          return React.cloneElement(child, { map, clusterer });
        }
      })}
    </>
  );
}

const Marker: React.FC<
  google.maps.MarkerOptions & {
    onCLick: () => void;
    clusterer?: MarkerClustererClass;
  }
> = (options) => {
  const { onCLick, clusterer, map, position, ...otherMarkerOptions } = options;
  const [marker, setMarker] = React.useState<google.maps.Marker>();

  React.useEffect(() => {
    if (!marker) {
      const newMarker = new window.google.maps.Marker();
      setMarker(newMarker);
      newMarker.addListener('click', onCLick);
    }

    return () => {
      if (marker) {
        marker.setMap(null);
        clusterer?.removeMarker(marker, false);
      }
    };
  }, [marker]);

  React.useEffect(() => {
    if (marker) {
      marker.setMap(map || null);
    }
  }, [marker, map]);

  React.useEffect(() => {
    if (marker) {
      marker.setPosition(position);
    }
  }, [marker, position && `${position?.lat},${position?.lng}`]);

  React.useEffect(() => {
    if (marker) {
      clusterer?.addMarker(marker, false);
    }
  }, [marker, clusterer]);

  React.useEffect(() => {
    if (marker) {
      marker.setOptions(otherMarkerOptions);
    }
  }, [marker, otherMarkerOptions]);

  return null;
};
